.text-carousel {
  padding-top: 2rem;
}
.text-carousel .block-header .subtitle {
  font-weight: 600;
  color: #006633;
}
.text-carousel .block-header .title {
  font-weight: 600;
  margin-bottom: 3rem;
  max-width: 650px;
}
.text-carousel .text-carousel-wrapper .slick-list .slick-track {
  display: flex;
  gap: 0.75rem;
  margin-bottom: 4rem;
}
.text-carousel .text-carousel-wrapper .slick-list .slick-track .single-carousel-item-wrapper {
  padding: 0 4px;
}
.text-carousel .text-carousel-wrapper .slick-list .slick-track .single-carousel-item-wrapper .single-carousel-item {
  position: relative;
  overflow: hidden;
  height: 335px;
  padding: 2rem;
  border-radius: 5px;
  background-color: #FFFFFF;
  box-shadow: 0 4px 12px 0 rgba(0, 0, 0, 0.1);
}
.text-carousel .text-carousel-wrapper .slick-list .slick-track .single-carousel-item-wrapper .single-carousel-item h5.text-carousel-title {
  font-weight: 600;
  color: #0D1A29;
  margin-bottom: 2.5rem;
}
.text-carousel .text-carousel-wrapper .slick-list .slick-track .single-carousel-item-wrapper .single-carousel-item p.text-carousel-text {
  color: #4C6078;
}
.text-carousel .text-carousel-wrapper .slick-prev {
  width: 48px;
  height: 48px;
  top: -75px;
  left: 980px;
}
.text-carousel .text-carousel-wrapper .slick-next {
  width: 48px;
  height: 48px;
  top: -75px;
  right: 180px;
}
.text-carousel.single-vacature .block-header {
  margin-top: 6rem;
}
.text-carousel.single-vacature .block-header .subtitle {
  font-weight: 600;
  color: #B7CD00;
}
.text-carousel.single-vacature .block-header .title {
  font-weight: 600;
  margin-bottom: 3rem;
}
.text-carousel.single-vacature .text-carousel-wrapper .slick-list .slick-track .single-carousel-item-wrapper .single-carousel-item {
  height: auto;
  background-color: #F5F5F7;
}
.text-carousel.indent-left .block-header,
.text-carousel.indent-left .slick-list {
  margin-left: 200px;
}
.text-carousel.right-edge .slick-list {
  margin-right: calc((100% - 100vw) / 2 + 8px);
}
@media only screen and (max-width: 768px) {
  .text-carousel {
    padding-top: 2.5rem;
  }
  .text-carousel .text-carousel-wrapper .slick-arrow {
    display: none !important;
  }
  .text-carousel .text-carousel-wrapper .slick-list .slick-track .single-carousel-item-wrapper .single-carousel-item {
    height: 360px;
  }
  .text-carousel.indent-left .block-header,
  .text-carousel.text-carousel.indent-left .slick-list {
    margin-left: inherit !important;
  }
}
